*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body{
  background-color: white;
  font-family: 'Inter', sans-serif;
  font-size: 20px;
}


select:focus {
  outline: none;
}

a{
  display: block;
  margin: auto;
}

h1{ 
  margin: 2em 0 0 0;
}

table, th, td{
  border: 1px solid #dddddd;
}


table{
  width: 90%;
  margin: 3em auto;
  border-collapse: collapse;
}

th,td{
  height: 3em;
  width: 5em;
}

