.my-requests{
    width: 85%;
    direction: rtl;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.my-requests .waiting{
    background-color: rgb(207, 207, 207);
}
.my-requests .confirmed{
    background-color: rgb(141, 255, 131);
}
.my-requests .not-confirmed{
    background-color: rgb(255, 127, 127);
}

.td-icon-delete{
    width: 20px;
}

.td-icon-delete svg path{
    cursor: pointer;
    color: rgb(204, 204, 204);
}
.td-icon-delete svg path:hover{
    color: rgb(77, 77, 77);
}